import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Save, RefreshCw, AlertTriangle, CheckCircle } from 'lucide-react';
import Card from './Card';
import Button from './Button';
import axios from 'axios';

const Settings = () => {
  const [settings, setSettings] = useState({
    snmpTimeout: 5,
    snmpRetries: 2,
    snmpVersion: '2c',
    testConcurrency: 3,
    aiEnabled: true,
    alertThresholds: {
      minDownlink: 50,
      minUplink: 25,
      minSnr: 15
    },
    cnmaestroSettings: {
      apiUrl: 'https://cnmaestro.gvec.net/api/v2/',
      timeout: 30,
      retries: 3
    }
  });
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [testConnection, setTestConnection] = useState(false);
  const [connectionStatus, setConnectionStatus] = useState(null);

  useEffect(() => {
    fetchSettings();
  }, []);

  const fetchSettings = async () => {
    try {
      const response = await axios.get('/linktest/api/settings');
      setSettings(response.data);
    } catch (error) {
      console.error('Failed to fetch settings:', error);
    } finally {
      setLoading(false);
    }
  };

  const saveSettings = async () => {
    setSaving(true);
    try {
      await axios.put('/linktest/api/settings', settings);
      setConnectionStatus({ type: 'success', message: 'Settings saved successfully' });
    } catch (error) {
      console.error('Failed to save settings:', error);
      setConnectionStatus({ type: 'error', message: 'Failed to save settings' });
    } finally {
      setSaving(false);
    }
  };

  const testConnections = async () => {
    setTestConnection(true);
    try {
      const response = await axios.post('/linktest/api/test-connections');
      setConnectionStatus({
        type: response.data.success ? 'success' : 'error',
        message: response.data.message,
        details: response.data.details
      });
    } catch (error) {
      console.error('Failed to test connections:', error);
      setConnectionStatus({
        type: 'error',
        message: 'Connection test failed',
        details: { database: false, influxdb: false, cnmaestro: false }
      });
    } finally {
      setTestConnection(false);
    }
  };

  const updateSetting = (path, value) => {
    const keys = path.split('.');
    const newSettings = { ...settings };
    let current = newSettings;
    
    for (let i = 0; i < keys.length - 1; i++) {
      current = current[keys[i]];
    }
    
    current[keys[keys.length - 1]] = value;
    setSettings(newSettings);
  };

  if (loading) {
    return (
      <div className="flex justify-center items-center h-64">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-3xl font-bold text-gray-900">Settings</h1>
        <div className="flex space-x-2">
          <Button onClick={testConnections} variant="outline" disabled={testConnection}>
            {testConnection ? (
              <RefreshCw className="w-4 h-4 mr-2 animate-spin" />
            ) : (
              <RefreshCw className="w-4 h-4 mr-2" />
            )}
            Test Connections
          </Button>
          <Button onClick={saveSettings} disabled={saving}>
            {saving ? (
              <RefreshCw className="w-4 h-4 mr-2 animate-spin" />
            ) : (
              <Save className="w-4 h-4 mr-2" />
            )}
            Save Settings
          </Button>
        </div>
      </div>

      {connectionStatus && (
        <motion.div
          initial={{ opacity: 0, y: -10 }}
          animate={{ opacity: 1, y: 0 }}
          className={`p-4 rounded-lg border ${
            connectionStatus.type === 'success'
              ? 'bg-green-50 border-green-200 text-green-800'
              : 'bg-red-50 border-red-200 text-red-800'
          }`}
        >
          <div className="flex items-center">
            {connectionStatus.type === 'success' ? (
              <CheckCircle className="w-5 h-5 mr-2" />
            ) : (
              <AlertTriangle className="w-5 h-5 mr-2" />
            )}
            <span className="font-medium">{connectionStatus.message}</span>
          </div>
          {connectionStatus.details && (
            <div className="mt-2 text-sm">
              <div>Database: {connectionStatus.details.database ? '' : ''}</div>
              <div>InfluxDB: {connectionStatus.details.influxdb ? '' : ''}</div>
              <div>cnMaestro: {connectionStatus.details.cnmaestro ? '' : ''}</div>
            </div>
          )}
        </motion.div>
      )}

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <Card title="SNMP Configuration">
          <div className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Timeout (seconds)
              </label>
              <input
                type="number"
                min="1"
                max="30"
                value={settings.snmpTimeout}
                onChange={(e) => updateSetting('snmpTimeout', parseInt(e.target.value))}
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Retries
              </label>
              <input
                type="number"
                min="0"
                max="5"
                value={settings.snmpRetries}
                onChange={(e) => updateSetting('snmpRetries', parseInt(e.target.value))}
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                SNMP Version
              </label>
              <select
                value={settings.snmpVersion}
                onChange={(e) => updateSetting('snmpVersion', e.target.value)}
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              >
                <option value="1">v1</option>
                <option value="2c">v2c</option>
                <option value="3">v3</option>
              </select>
            </div>
          </div>
        </Card>

        <Card title="Test Configuration">
          <div className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Concurrent Tests
              </label>
              <input
                type="number"
                min="1"
                max="10"
                value={settings.testConcurrency}
                onChange={(e) => updateSetting('testConcurrency', parseInt(e.target.value))}
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
              <p className="text-sm text-gray-500 mt-1">
                Maximum number of simultaneous tests
              </p>
            </div>
            
            <div>
              <label className="flex items-center">
                <input
                  type="checkbox"
                  checked={settings.aiEnabled}
                  onChange={(e) => updateSetting('aiEnabled', e.target.checked)}
                  className="mr-2"
                />
                <span className="text-sm font-medium text-gray-700">
                  Enable AI Analysis
                </span>
              </label>
              <p className="text-sm text-gray-500 mt-1">
                Anomaly detection and performance forecasting
              </p>
            </div>
          </div>
        </Card>

        <Card title="Alert Thresholds">
          <div className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Minimum Downlink (Mbps)
              </label>
              <input
                type="number"
                min="0"
                value={settings.alertThresholds.minDownlink}
                onChange={(e) => updateSetting('alertThresholds.minDownlink', parseInt(e.target.value))}
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Minimum Uplink (Mbps)
              </label>
              <input
                type="number"
                min="0"
                value={settings.alertThresholds.minUplink}
                onChange={(e) => updateSetting('alertThresholds.minUplink', parseInt(e.target.value))}
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Minimum SNR (dB)
              </label>
              <input
                type="number"
                min="0"
                value={settings.alertThresholds.minSnr}
                onChange={(e) => updateSetting('alertThresholds.minSnr', parseInt(e.target.value))}
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>
          </div>
        </Card>

        <Card title="cnMaestro Integration">
          <div className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                API URL
              </label>
              <input
                type="url"
                value={settings.cnmaestroSettings.apiUrl}
                onChange={(e) => updateSetting('cnmaestroSettings.apiUrl', e.target.value)}
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Timeout (seconds)
              </label>
              <input
                type="number"
                min="5"
                max="120"
                value={settings.cnmaestroSettings.timeout}
                onChange={(e) => updateSetting('cnmaestroSettings.timeout', parseInt(e.target.value))}
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Retries
              </label>
              <input
                type="number"
                min="0"
                max="5"
                value={settings.cnmaestroSettings.retries}
                onChange={(e) => updateSetting('cnmaestroSettings.retries', parseInt(e.target.value))}
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>
          </div>
        </Card>
      </div>
    </div>
  );
};

export default Settings;