import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Play, Pause, Settings, RefreshCw } from 'lucide-react';
import Card from './Card';
import Button from './Button';
import axios from 'axios';

const Scheduler = () => {
  const [accessPoints, setAccessPoints] = useState([]);
  const [selectedAP, setSelectedAP] = useState('');
  const [testType, setTestType] = useState('individual');
  const [isRunning, setIsRunning] = useState(false);
  const [activeJobs, setActiveJobs] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchAccessPoints();
    fetchActiveJobs();
    const interval = setInterval(fetchActiveJobs, 5000);
    return () => clearInterval(interval);
  }, []);

  const fetchAccessPoints = async () => {
    try {
      const response = await axios.get('/linktest/api/access-points');
      setAccessPoints(response.data);
    } catch (error) {
      console.error('Failed to fetch access points:', error);
      setAccessPoints([
        { apMac: '00:04:56:AA:BB:CC', name: 'WestHill-AP1', model: '450v', isOnline: true, smCount: 8 },
        { apMac: '00:04:56:DD:EE:FF', name: 'EastTower-AP2', model: '450m', isOnline: true, smCount: 12 },
        { apMac: '00:04:56:GG:HH:II', name: 'SouthSite-AP3', model: '450v', isOnline: false, smCount: 6 }
      ]);
    } finally {
      setLoading(false);
    }
  };

  const fetchActiveJobs = async () => {
    try {
      const response = await axios.get('/linktest/api/active-jobs');
      setActiveJobs(response.data);
    } catch (error) {
      console.error('Failed to fetch active jobs:', error);
      setActiveJobs([
        { id: 1, apMac: '00:04:56:AA:BB:CC', smMac: '00:04:56:11:22:33', status: 'running', progress: 65 },
        { id: 2, apMac: '00:04:56:DD:EE:FF', smMac: '00:04:56:44:55:66', status: 'queued', progress: 0 }
      ]);
    }
  };

  const startTest = async () => {
    if (!selectedAP) return;
    
    setIsRunning(true);
    try {
      await axios.post('/linktest/api/run-test', {
        apMac: selectedAP,
        testType
      });
      fetchActiveJobs();
    } catch (error) {
      console.error('Failed to start test:', error);
    } finally {
      setIsRunning(false);
    }
  };

  const stopTest = async (jobId) => {
    try {
      await axios.post(`/linktest/api/stop-test/${jobId}`);
      fetchActiveJobs();
    } catch (error) {
      console.error('Failed to stop test:', error);
    }
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-3xl font-bold text-gray-900">Test Scheduler</h1>
        <Button onClick={fetchActiveJobs} variant="outline">
          <RefreshCw className="w-4 h-4 mr-2" />
          Refresh
        </Button>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <Card title="Schedule New Test">
          <div className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Access Point
              </label>
              <select
                value={selectedAP}
                onChange={(e) => setSelectedAP(e.target.value)}
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              >
                <option value="">Select an Access Point</option>
                {accessPoints.map((ap) => (
                  <option key={ap.apMac} value={ap.apMac} disabled={!ap.isOnline}>
                    {ap.name} ({ap.model}) - {ap.smCount} SMs {!ap.isOnline && '(Offline)'}
                  </option>
                ))}
              </select>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Test Type
              </label>
              <div className="space-y-2">
                <label className="flex items-center">
                  <input
                    type="radio"
                    value="individual"
                    checked={testType === 'individual'}
                    onChange={(e) => setTestType(e.target.value)}
                    className="mr-2"
                  />
                  <span className="text-sm">Individual (450v: one SM, 450m: sequential)</span>
                </label>
                <label className="flex items-center">
                  <input
                    type="radio"
                    value="flood"
                    checked={testType === 'flood'}
                    onChange={(e) => setTestType(e.target.value)}
                    className="mr-2"
                  />
                  <span className="text-sm">Flood Mode (450m only: simultaneous)</span>
                </label>
              </div>
            </div>

            <Button
              onClick={startTest}
              disabled={!selectedAP || isRunning || loading}
              className="w-full"
            >
              {isRunning ? (
                <>
                  <RefreshCw className="w-4 h-4 mr-2 animate-spin" />
                  Starting Test...
                </>
              ) : (
                <>
                  <Play className="w-4 h-4 mr-2" />
                  Start Test
                </>
              )}
            </Button>
          </div>
        </Card>

        <Card title="Active Tests">
          <div className="space-y-4">
            {activeJobs.length === 0 ? (
              <p className="text-gray-500 text-center py-4">No active tests</p>
            ) : (
              activeJobs.map((job) => (
                <motion.div
                  key={job.id}
                  initial={{ opacity: 0, y: 10 }}
                  animate={{ opacity: 1, y: 0 }}
                  className="p-4 bg-gray-50 rounded-lg"
                >
                  <div className="flex items-center justify-between mb-2">
                    <div>
                      <p className="text-sm font-medium text-gray-900">
                        {job.smMac}
                      </p>
                      <p className="text-xs text-gray-500">
                        AP: {job.apMac}
                      </p>
                    </div>
                    <div className="flex items-center space-x-2">
                      <span className={`px-2 py-1 text-xs rounded-full ${
                        job.status === 'running' ? 'bg-blue-100 text-blue-800' :
                        job.status === 'queued' ? 'bg-yellow-100 text-yellow-800' :
                        'bg-gray-100 text-gray-800'
                      }`}>
                        {job.status}
                      </span>
                      {job.status === 'running' && (
                        <Button
                          onClick={() => stopTest(job.id)}
                          variant="outline"
                          size="sm"
                        >
                          <Pause className="w-3 h-3" />
                        </Button>
                      )}
                    </div>
                  </div>
                  {job.status === 'running' && (
                    <div className="w-full bg-gray-200 rounded-full h-2">
                      <div
                        className="bg-blue-500 h-2 rounded-full transition-all duration-300"
                        style={{ width: `${job.progress}%` }}
                      />
                    </div>
                  )}
                </motion.div>
              ))
            )}
          </div>
        </Card>
      </div>

      <Card title="Access Points Overview">
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Name
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Model
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Status
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  SMs
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Actions
                </th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {accessPoints.map((ap) => (
                <tr key={ap.apMac}>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                    {ap.name}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                    {ap.model}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <span className={`px-2 py-1 text-xs rounded-full ${
                      ap.isOnline ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'
                    }`}>
                      {ap.isOnline ? 'Online' : 'Offline'}
                    </span>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                    {ap.smCount}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                    <Button
                      onClick={() => setSelectedAP(ap.apMac)}
                      variant="outline"
                      size="sm"
                      disabled={!ap.isOnline}
                    >
                      <Settings className="w-3 h-3 mr-1" />
                      Select
                    </Button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </Card>
    </div>
  );
};

export default Scheduler;