import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Search, Filter, Download, Calendar } from 'lucide-react';
import Card from './Card';
import Button from './Button';
import axios from 'axios';

const History = () => {
  const [testResults, setTestResults] = useState([]);
  const [filteredResults, setFilteredResults] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState('all');
  const [dateRange, setDateRange] = useState('7d');
  const [loading, setLoading] = useState(true);
  const [currentPage, setCurrentPage] = useState(1);
  const resultsPerPage = 10;

  useEffect(() => {
    fetchTestHistory();
  }, [dateRange]);

  useEffect(() => {
    filterResults();
  }, [testResults, searchTerm, statusFilter]);

  const fetchTestHistory = async () => {
    setLoading(true);
    try {
      const response = await axios.get(`/linktest/api/history?range=${dateRange}`);
      setTestResults(response.data);
    } catch (error) {
      console.error('Failed to fetch test history:', error);
      setTestResults([
        {
          id: 1,
          apMac: '00:04:56:AA:BB:CC',
          smMac: '00:04:56:11:22:33',
          apName: 'WestHill-AP1',
          model: '450v',
          status: 'success',
          downlinkMbps: 128.5,
          uplinkMbps: 63.2,
          snrDl: 26.1,
          snrUl: 24.8,
          testStarted: '2024-01-15T10:30:00Z',
          testCompleted: '2024-01-15T10:35:00Z',
          duration: 300
        },
        {
          id: 2,
          apMac: '00:04:56:DD:EE:FF',
          smMac: '00:04:56:44:55:66',
          apName: 'EastTower-AP2',
          model: '450m',
          status: 'failed',
          downlinkMbps: 0,
          uplinkMbps: 0,
          snrDl: 0,
          snrUl: 0,
          testStarted: '2024-01-15T10:25:00Z',
          testCompleted: '2024-01-15T10:26:00Z',
          duration: 60,
          errorMessage: 'SNMP timeout'
        },
        {
          id: 3,
          apMac: '00:04:56:GG:HH:II',
          smMac: '00:04:56:77:88:99',
          apName: 'SouthSite-AP3',
          model: '450v',
          status: 'success',
          downlinkMbps: 95.8,
          uplinkMbps: 45.6,
          snrDl: 22.3,
          snrUl: 21.7,
          testStarted: '2024-01-15T10:20:00Z',
          testCompleted: '2024-01-15T10:25:00Z',
          duration: 300
        }
      ]);
    } finally {
      setLoading(false);
    }
  };

  const filterResults = () => {
    let filtered = testResults;

    if (searchTerm) {
      filtered = filtered.filter(result => 
        result.apName.toLowerCase().includes(searchTerm.toLowerCase()) ||
        result.smMac.toLowerCase().includes(searchTerm.toLowerCase()) ||
        result.apMac.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }

    if (statusFilter !== 'all') {
      filtered = filtered.filter(result => result.status === statusFilter);
    }

    setFilteredResults(filtered);
    setCurrentPage(1);
  };

  const exportResults = async () => {
    try {
      const response = await axios.get('/linktest/api/export-history', {
        params: { range: dateRange, format: 'csv' },
        responseType: 'blob'
      });
      
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', `link-test-history-${dateRange}.csv`);
      document.body.appendChild(link);
      link.click();
      link.remove();
    } catch (error) {
      console.error('Failed to export results:', error);
    }
  };

  const paginatedResults = filteredResults.slice(
    (currentPage - 1) * resultsPerPage,
    currentPage * resultsPerPage
  );

  const totalPages = Math.ceil(filteredResults.length / resultsPerPage);

  const getStatusColor = (status) => {
    switch (status) {
      case 'success': return 'bg-green-100 text-green-800';
      case 'failed': return 'bg-red-100 text-red-800';
      case 'running': return 'bg-blue-100 text-blue-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-3xl font-bold text-gray-900">Test History</h1>
        <div className="flex space-x-2">
          <Button onClick={exportResults} variant="outline">
            <Download className="w-4 h-4 mr-2" />
            Export
          </Button>
          <Button onClick={fetchTestHistory}>
            Refresh
          </Button>
        </div>
      </div>

      <Card>
        <div className="flex flex-col sm:flex-row gap-4 mb-6">
          <div className="flex-1">
            <div className="relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-4 h-4" />
              <input
                type="text"
                placeholder="Search by AP name, MAC address..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>
          </div>
          
          <div className="flex space-x-2">
            <select
              value={statusFilter}
              onChange={(e) => setStatusFilter(e.target.value)}
              className="px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            >
              <option value="all">All Status</option>
              <option value="success">Success</option>
              <option value="failed">Failed</option>
              <option value="running">Running</option>
            </select>
            
            <select
              value={dateRange}
              onChange={(e) => setDateRange(e.target.value)}
              className="px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            >
              <option value="1d">Last 24 hours</option>
              <option value="7d">Last 7 days</option>
              <option value="30d">Last 30 days</option>
              <option value="90d">Last 90 days</option>
            </select>
          </div>
        </div>

        {loading ? (
          <div className="text-center py-8">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600 mx-auto"></div>
            <p className="text-gray-500 mt-2">Loading test history...</p>
          </div>
        ) : (
          <>
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      AP / SM
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Status
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Capacity (Mbps)
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      SNR (dB)
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Duration
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Completed
                    </th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-gray-200">
                  {paginatedResults.map((result) => (
                    <motion.tr
                      key={result.id}
                      initial={{ opacity: 0 }}
                      animate={{ opacity: 1 }}
                      className="hover:bg-gray-50"
                    >
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div>
                          <div className="text-sm font-medium text-gray-900">
                            {result.apName} ({result.model})
                          </div>
                          <div className="text-sm text-gray-500">
                            SM: {result.smMac}
                          </div>
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span className={`px-2 py-1 text-xs rounded-full ${getStatusColor(result.status)}`}>
                          {result.status}
                        </span>
                        {result.errorMessage && (
                          <div className="text-xs text-red-600 mt-1">
                            {result.errorMessage}
                          </div>
                        )}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                        <div> {result.downlinkMbps}</div>
                        <div> {result.uplinkMbps}</div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                        <div>DL: {result.snrDl}</div>
                        <div>UL: {result.snrUl}</div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {Math.floor(result.duration / 60)}m {result.duration % 60}s
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {new Date(result.testCompleted).toLocaleString()}
                      </td>
                    </motion.tr>
                  ))}
                </tbody>
              </table>
            </div>

            {totalPages > 1 && (
              <div className="flex items-center justify-between mt-6">
                <div className="text-sm text-gray-700">
                  Showing {((currentPage - 1) * resultsPerPage) + 1} to {Math.min(currentPage * resultsPerPage, filteredResults.length)} of {filteredResults.length} results
                </div>
                <div className="flex space-x-2">
                  <Button
                    onClick={() => setCurrentPage(Math.max(1, currentPage - 1))}
                    disabled={currentPage === 1}
                    variant="outline"
                    size="sm"
                  >
                    Previous
                  </Button>
                  <Button
                    onClick={() => setCurrentPage(Math.min(totalPages, currentPage + 1))}
                    disabled={currentPage === totalPages}
                    variant="outline"
                    size="sm"
                  >
                    Next
                  </Button>
                </div>
              </div>
            )}
          </>
        )}
      </Card>
    </div>
  );
};

export default History;