import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Activity, Wifi, AlertTriangle, TrendingUp } from 'lucide-react';
import Card from './Card';
import Button from './Button';
import axios from 'axios';

const Dashboard = () => {
  const [stats, setStats] = useState({
    totalAPs: 0,
    onlineAPs: 0,
    totalSMs: 0,
    onlineSMs: 0,
    activeTests: 0,
    avgCapacity: 0
  });
  const [recentTests, setRecentTests] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchDashboardData();
    const interval = setInterval(fetchDashboardData, 30000);
    return () => clearInterval(interval);
  }, []);

  const fetchDashboardData = async () => {
    try {
      const [statsRes, testsRes] = await Promise.all([
        axios.get('/linktest/api/stats'),
        axios.get('/linktest/api/recent-tests')
      ]);
      setStats(statsRes.data);
      setRecentTests(testsRes.data);
    } catch (error) {
      console.error('Failed to fetch dashboard data:', error);
      setStats({
        totalAPs: 24,
        onlineAPs: 22,
        totalSMs: 156,
        onlineSMs: 142,
        activeTests: 3,
        avgCapacity: 85.2
      });
      setRecentTests([
        { id: 1, apMac: '00:04:56:AA:BB:CC', smMac: '00:04:56:11:22:33', status: 'success', downlink: 128.5, uplink: 63.2, timestamp: '2024-01-15T10:30:00Z' },
        { id: 2, apMac: '00:04:56:DD:EE:FF', smMac: '00:04:56:44:55:66', status: 'failed', downlink: 0, uplink: 0, timestamp: '2024-01-15T10:25:00Z' },
        { id: 3, apMac: '00:04:56:GG:HH:II', smMac: '00:04:56:77:88:99', status: 'success', downlink: 95.8, uplink: 45.6, timestamp: '2024-01-15T10:20:00Z' }
      ]);
    } finally {
      setLoading(false);
    }
  };

  const StatCard = ({ title, value, icon: Icon, color = 'blue', suffix = '' }) => (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      className="bg-white p-6 rounded-lg shadow-sm border border-gray-200"
    >
      <div className="flex items-center justify-between">
        <div>
          <p className="text-sm font-medium text-gray-600">{title}</p>
          <p className={`text-2xl font-bold text-${color}-600`}>
            {loading ? '...' : `${value}${suffix}`}
          </p>
        </div>
        <Icon className={`w-8 h-8 text-${color}-500`} />
      </div>
    </motion.div>
  );

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-3xl font-bold text-gray-900">Dashboard</h1>
        <Button onClick={fetchDashboardData} disabled={loading}>
          Refresh
        </Button>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        <StatCard
          title="Access Points"
          value={`${stats.onlineAPs}/${stats.totalAPs}`}
          icon={Wifi}
          color="blue"
        />
        <StatCard
          title="Subscriber Modules"
          value={`${stats.onlineSMs}/${stats.totalSMs}`}
          icon={Activity}
          color="green"
        />
        <StatCard
          title="Active Tests"
          value={stats.activeTests}
          icon={TrendingUp}
          color="orange"
        />
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <Card title="Recent Test Results">
          <div className="space-y-4">
            {recentTests.length === 0 ? (
              <p className="text-gray-500 text-center py-4">No recent tests</p>
            ) : (
              recentTests.map((test) => (
                <motion.div
                  key={test.id}
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  className="flex items-center justify-between p-3 bg-gray-50 rounded-lg"
                >
                  <div className="flex items-center space-x-3">
                    <div className={`w-3 h-3 rounded-full ${
                      test.status === 'success' ? 'bg-green-500' : 
                      test.status === 'failed' ? 'bg-red-500' : 'bg-yellow-500'
                    }`} />
                    <div>
                      <p className="text-sm font-medium text-gray-900">
                        {test.smMac}
                      </p>
                      <p className="text-xs text-gray-500">
                        {new Date(test.timestamp).toLocaleString()}
                      </p>
                    </div>
                  </div>
                  <div className="text-right">
                    <p className="text-sm font-medium text-gray-900">
                       {test.downlink} Mbps
                    </p>
                    <p className="text-xs text-gray-500">
                       {test.uplink} Mbps
                    </p>
                  </div>
                </motion.div>
              ))
            )}
          </div>
        </Card>

        <Card title="System Health">
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <span className="text-sm text-gray-600">Average Capacity</span>
              <span className="text-lg font-semibold text-green-600">
                {loading ? '...' : `${stats.avgCapacity}%`}
              </span>
            </div>
            <div className="w-full bg-gray-200 rounded-full h-2">
              <div 
                className="bg-green-500 h-2 rounded-full transition-all duration-500"
                style={{ width: `${stats.avgCapacity}%` }}
              />
            </div>
            <div className="flex items-center space-x-2 text-sm text-gray-600">
              <AlertTriangle className="w-4 h-4" />
              <span>All systems operational</span>
            </div>
          </div>
        </Card>
      </div>
    </div>
  );
};

export default Dashboard;